html,
body {
  margin: 0;
  min-height: 100%;
  padding: 0;
}
body {
  background: #fff url(../img/pew-bg.png) no-repeat bottom 0 left 5px;
  box-sizing: border-box;
  color: #333;
  display: flex;
  flex-direction: column;
  font: normal 16px/165% -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  min-height: 100vh;
}
body.users-layout {
  align-items: center;
  justify-content: center;
}
header,
footer {
  background-color: #333;
  color: #b3b3b3;
  padding: 0;
}
a {
  color: #f93;
  text-decoration: none;
}
a:hover {
  color: #e57300;
  text-decoration: underline;
}
#main {
  display: flex;
  flex: 1;
  flex-direction: column;
  padding: 50px;
}
header {
  align-items: center;
  display: flex;
  flex-basis: 55px;
  justify-content: space-between;
}
header .menu a {
  padding: 15px;
}
footer {
  flex-basis: 40px;
  font-size: 11px;
  line-height: 40px;
  text-align: center;
}
footer p {
  margin: 0;
  padding: 0;
}
.app-title {
  margin: 0;
  padding: 15px;
  text-shadow: 0 0 3px rgba(180, 210, 120, 0.7), 0 0 6px rgba(250, 255, 250, 0.4);
  text-transform: uppercase;
}
.app-title a {
  color: #bd7;
  font-weight: bold;
  text-decoration: none;
}
.user-wrapper {
  align-items: center;
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
}
.user-wrapper form {
  background-color: rgba(51, 51, 51, 0.95);
  border-radius: 2px;
  color: #fff;
  padding: 25px;
}
.user-wrapper label {
  display: block;
  font-size: 0.9em;
}
.user-wrapper input[type="text"],
.user-wrapper input[type="email"],
.user-wrapper input[type="password"],
.user-wrapper input[type=""] {
  min-width: 300px;
  padding: 6px;
}
form > h1,
form > h2,
form > h3 {
  color: #bd7;
  margin-top: 0;
}
form .form-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 24px;
}
form .form-actions {
  align-items: center;
  display: flex;
  justify-content: flex-end;
}
form .form-actions button,
form .form-actions input[type="submit"],
form .form-actions input[type="reset"] {
  margin-left: 12px;
}
form button,
form input[type="submit"],
form input[type="reset"] {
  background-color: #bd7;
  border: 1px solid #bd7;
  color: rgba(0, 0, 0, 0.9);
  cursor: pointer;
  font-weight: bold;
  letter-spacing: 0.1em;
  padding: 12px 24px;
  text-transform: uppercase;
}
